import * as React from 'react';
import { Link } from 'react-router-dom';
import { translate } from 'react-i18next';
import { connect } from 'react-redux';
import { fetchHeaderMenusWP } from '../actions/headerMenus';
import mapStateToProps from '../utils/mapStateToProps';
import mapDispatchToProps from '../utils/mapDispatchToProps';
import * as _ from 'lodash';

const STORAGE_KEY_HEADER_MENU_LEFT_1 = '__left_header_menu1';
const STORAGE_KEY_HEADER_MENU_LEFT_2 = '__left_header_menu2';
const STORAGE_KEY_HEADER_MENU_LEFT_3 = '__left_header_menu3';
const STORAGE_KEY_HEADER_MENU_RIGHT_1 = '__right_header_menu1';
const STORAGE_KEY_HEADER_MENU_RIGHT_2 = '__right_header_menu2';
const STORAGE_KEY_HEADER_MENU_RIGHT_3 = '__right_header_menu3';
let STORAGE_KEY_PREFIX_LANGUAGE = 'vn';

class Header extends React.Component<any, any> {

	constructor(props) {
		super(props);
		this.state = {
			active: false
		};
		STORAGE_KEY_PREFIX_LANGUAGE = localStorage.getItem('i18nextLng');
	}

	componentDidMount(): void {
		this.props.fetchHeaderMenusWP({ post_type: 'menu', number: 500 });
	}

	changeLanguage = (value) => {
		this.props.i18n.changeLanguage(value);
		document.location.href="/";
	};

	showMenu = () => {
		this.setState({
			active: !this.state.active
		});
	};

	render() {
		const { headerMenus } = this.props;
		let prefix = localStorage.getItem(STORAGE_KEY_PREFIX_LANGUAGE);

		if (prefix == undefined) {
			prefix = 'vn';
		}

		if (headerMenus.data.length > 0) {
			let headersLeft = _.filter(headerMenus.data, function (post) {
				return _.some(post.custom_fields, {
						'key': 'position',
						'value': 'lheader'
					}) && _.some(post.custom_fields, { 'key': 'language', 'value': STORAGE_KEY_PREFIX_LANGUAGE });
			});

			headersLeft.sort(function (menuA, menuB) {
				return menuA.post_parent - menuB.post_parent;
			});

			let headersRight = _.filter(headerMenus.data, function (post) {
				return _.some(post.custom_fields, {
						'key': 'position',
						'value': 'rheader'
					}) && _.some(post.custom_fields, { 'key': 'language', 'value': STORAGE_KEY_PREFIX_LANGUAGE });
			});
			headersRight.sort(function (menuA, menuB) {
				return menuA.post_parent - menuB.post_parent;
			});

			//Iterating left headers then store data in local storage
			headersLeft.map((item, index) => {
				let jsonItem = {
					title: '',
					link: '',
					type: 0
				};

				let customFields = item.custom_fields;
				customFields.map((cField) => {
					if (cField.key === 'link') {
						var pathname = new URL(cField.value).pathname;
						var link = cField.value;
						if(item.post_excerpt == 0) {
							link = pathname
						}
						jsonItem.link = link;
					}
				});

				jsonItem.title = item.post_title;
				jsonItem.type = item.post_excerpt;

				if (index == 0) {
					localStorage.setItem(prefix + STORAGE_KEY_HEADER_MENU_LEFT_1, JSON.stringify(jsonItem));
				} else if (index == 1) {
					localStorage.setItem(prefix + STORAGE_KEY_HEADER_MENU_LEFT_2, JSON.stringify(jsonItem));
				} else {
					localStorage.setItem(prefix + STORAGE_KEY_HEADER_MENU_LEFT_3, JSON.stringify(jsonItem));
				}
			});

			//Iterating right headers then store data in local storage
			headersRight.map((item, index) => {
				let jsonItem = {
					title: '',
					link: '',
					type: 0
				};

				let customFields = item.custom_fields;
				customFields.map((cField) => {
					if (cField.key === 'link') {
						var pathname = new URL(cField.value).pathname;
						var link = cField.value;
						if(item.post_excerpt == 0) {
							link = pathname
						}
						jsonItem.link = link;
					}
				});

				jsonItem.title = item.post_title;
				jsonItem.type = item.post_excerpt;

				if (index == 0) {
					localStorage.setItem(prefix + STORAGE_KEY_HEADER_MENU_RIGHT_1, JSON.stringify(jsonItem));
				} else if (index == 1) {
					localStorage.setItem(prefix + STORAGE_KEY_HEADER_MENU_RIGHT_2, JSON.stringify(jsonItem));
				} else {
					localStorage.setItem(prefix + STORAGE_KEY_HEADER_MENU_RIGHT_3, JSON.stringify(jsonItem));
				}
			});
		}

		let about: any, drinks: any, stuffs: any, news: any, stores: any, contact: any;


		let left1JsonString = localStorage.getItem(prefix + STORAGE_KEY_HEADER_MENU_LEFT_1);

		if (left1JsonString == undefined) {
			about = {
				title: 'Giới Thiệu!',
				link: '/about',
				type: 0
			};
		} else {
			about = JSON.parse(left1JsonString);
		}

		let left2JsonString = localStorage.getItem(prefix + STORAGE_KEY_HEADER_MENU_LEFT_2);

		if (left2JsonString == undefined) {
			drinks = {
				title: 'Pha chế!',
				link: '/drinks',
				type: 0
			};
		} else {
			drinks = JSON.parse(left2JsonString);
		}

		let left3JsonString = localStorage.getItem(prefix + STORAGE_KEY_HEADER_MENU_LEFT_3);

		if (left3JsonString == undefined) {
			stuffs = {
				title: 'Tạp hóa!',
				link: '/stuffs',
				type: 0
			};
		} else {
			stuffs = JSON.parse(left3JsonString);
		}

		let right1JsonString = localStorage.getItem(prefix + STORAGE_KEY_HEADER_MENU_RIGHT_1);

		if (right1JsonString == undefined) {
			news = {
				title: 'Bảng Tin!',
				link: 'https://wificong.news.co',
				type: 1
			};
		} else {
			news = JSON.parse(right1JsonString);
		}

		let right2JsonString = localStorage.getItem(prefix + STORAGE_KEY_HEADER_MENU_RIGHT_2);

		if (right2JsonString == undefined) {
			stores = {
				title: 'Cửa Hàng!',
				link: '/stores',
				type: 0
			};
		} else {
			stores = JSON.parse(right2JsonString);
		}

		let right3JsonString = localStorage.getItem(prefix + STORAGE_KEY_HEADER_MENU_RIGHT_3);

		if (right3JsonString == undefined) {
			contact = {
				title: 'Liên hệ!',
				link: '/contact',
				type: 0
			};
		} else {
			contact = JSON.parse(right3JsonString);
		}

		return (
			<div>
				<div className="topMobile">
					<div className="container">
						<div onClick={this.showMenu} className={this.state.active ? 'buttonMenu-Mobile active' : 'buttonMenu-Mobile 123'}>
				        <span className="icon-menu">
				          <div className="bar1"/>
				          <div className="bar2"/>
				          <div className="bar3"/>
				        </span>
						</div>
						<div className="logoMobile">
							<Link to="/"><img src={require('../img/logo.png')} height={43}/></Link>
						</div>
						<div className="lang float-right lang-language">
							<span onClick={() => this.changeLanguage('vn')}><img src={require('../img/vn.png')} height={16}/></span>
							<span onClick={() => this.changeLanguage('vn')}><img src={require('../img/en.png')} height={16}/></span>
						</div>
					</div>
				</div>
				<div className="clearfix"/>
				<div className="header text-center">
					<div className="lang lang-language">
						<span onClick={() => this.changeLanguage('vn')}><img src={require('../img/vn.png')}
						                                                     height={16}/></span>
						<span onClick={() => this.changeLanguage('en')}><img src={require('../img/en.png')}
						                                                     height={16}/></span>
					</div>
					<div className="container" style={{ maxWidth: 1200 }}>
						<nav className={this.state.active ? 'active': ''}>
							<ul className="list-inline">
								<li className="list-inline-item">
									{about.type == 0 ? <Link to={about.link}>{about.title}</Link> :
										<a href={about.link} target='_blank'>{about.title}</a>}
								</li>
								<li className="list-inline-item">
									{drinks.type == 0 ? <Link to={drinks.link}>{drinks.title}</Link> :
										<a href={drinks.link} target='_blank'>{drinks.title}</a>}
								</li>
								<li className="list-inline-item">
									{stuffs.type == 0 ? <Link to={stuffs.link}>{stuffs.title}</Link> :
										<a href={stuffs.link} target='_blank'>{stuffs.title}</a>}
								</li>
								<li className="list-inline-item logo">
									<h1 className="logo">
										<Link to="/"><img src={require('../img/logo.png')} height={43}/></Link>
									</h1>
								</li>
								<li className="list-inline-item">
									{news.type == 0 ? <Link to={news.link}>{about.title}</Link> :
										<a href={news.link} target='_blank'>{news.title}</a>}
								</li>
								<li className="list-inline-item">
									{stores.type == 0 ? <Link to={stores.link}>{stores.title}</Link> :
										<a href={stores.link} target='_blank'>{stores.title}</a>}
								</li>
								<li className="list-inline-item">
									{contact.type == 0 ? <Link to={contact.link}>{contact.title}</Link> :
										<a href={contact.link} target='_blank'>{contact.title}</a>}
								</li>
							</ul>
						</nav>
					</div>
				</div>
				<div className="clearfix"/>
			</div>
		);
	}
}

export default connect(mapStateToProps([
	'headerMenus'
]), mapDispatchToProps({
	fetchHeaderMenusWP
}))(translate('translations')(Header));



