import * as React from 'react';
import { translate } from 'react-i18next';
import * as _ from 'lodash';
import OwlCarousel from 'react-owl-carousel2';
import api from '../services';

const slideOptions = {
	items: 1,
	margin: 0,
	dots: true,
	rewind: true,
	autoplay: true,
	loop: true
};

class HomeTopSlides extends React.Component<any, any> {

	constructor(props) {
		super(props);
		this.state = {
			data_slides: []
		};
	}

	componentDidMount() {
		api.getPosts({
			post_type: 'slide'
		}).then(res => {
			this.setState({
				data_slides: res.data
			});

			setTimeout(() => {
				api.getPosts({
					post_type: 'slide'
				}).then(res => {
					this.setState({
						data_slides: res.data
					});
				});
			}, 3000);
		});
	}

	render() {

		const { data_slides } = this.state;

		let filteredList = _.filter(data_slides, function (post) {
			return _.some(post.custom_fields, { 'value': localStorage.getItem('i18nextLng'), 'key': 'language' });
		});

		const slides = filteredList.map((item) => {
			var link = '';
			if (item.custom_fields.length > 0) {
				link = item.custom_fields[1]['value'];
				return (
					<div className="item" key={item.post_id}>
						<a href={link} target="_blank">
							<img src={require('../img/mask-slide1.png')}
							     style={{ background: `url(${item.post_thumbnail.link})  no-repeat top left/cover` }}/>
						</a>
					</div>
				);
			}
		});

		const owlSlides = filteredList.length == 0 ? <div></div> :
			<div>
				<OwlCarousel options={slideOptions}>
					{slides}
				</OwlCarousel>
				<div className="txtSlide">
					<div className="container">
						<div className="text-center">
							<h6 className="wow fadeInUp">{filteredList[0]['post_title']}</h6>
							<p className="wow fadeIn" data-wow-delay="0.5s">
								{filteredList[0]['post_content']}
							</p>
							<div className="more wow fadeIn" data-wow-delay="0.5s">
								<a href={filteredList[0]['custom_fields'][1]['value']} target="_blank" className="cl1">{this.props.t('view_more')}</a>
							</div>

						</div>
					</div>
				</div>
			</div>;

		return (
			<div>
				<div className="clearfix"></div>
				<div className="slide slideCover">
					{owlSlides}
				</div>
				<div className="clearfix"></div>
			</div>
		);
	}
}

export default translate('translations')(HomeTopSlides);



