import * as React from 'react';
import { Link } from 'react-router-dom';
import { translate } from 'react-i18next';

class HomeFooterCopyrights extends React.Component<any, any> {

	render() {
		return (
			<div>
                <div className="clearfix"></div>
                <div className="listSocial">
                    <div className="container">
                        <div className="row ">
                            <div className="col-sm-12">
                                <div className="social text-left">
                                    <div className="listSocial-c">
                                        <a href="#"><img src={require('../img/img-social1.png')} height="30"/></a>
                                        <a href="#"><img src={require('../img/img-social2.png')} height="30"/></a>
                                        <a href="#"><img src={require('../img/img-social3.png')} height="23"/></a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="copyright">
                    <div className="container">
                        <div className="row">
                            <div className="col-sm-12 text-left">
                                <div className="info-copyright ">
                                    ©2016 <span className="txtCafeCong">
	                                <img src={require('../img/congcafe-txt.png')} height="35"/></span> Co., Ltd | All Rights
                                    Reserved.
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="overlay"></div>
			</div>
		);
	}
}

export default translate('translations')(HomeFooterCopyrights);



